import express from 'express';
import compression from 'compression';
import bodyParser from 'body-parser';
import errorHandler from 'errorhandler';
import chalk from 'chalk';

class App {
  constructor() {
    this.app = express();
    this._setMiddlewares();
    this._setMetaParams();
  }

  _setMiddlewares() {
    this.app.use(compression());
    this.app.use(bodyParser.json());
    this.app.use(bodyParser.urlencoded({ extended: true }));
    this.app.use(errorHandler());
  }

  _setMetaParams() {
    this.app.set('host', process.env.OPENSHIFT_NODEJS_IP || '0.0.0.0');
    this.app.set('port', process.env.PORT || process.env.OPENSHIFT_NODEJS_PORT || 8081);
  }

  run() {
    this.app.listen(this.app.get('port'), () => {
      console.log('%s App is running at http://localhost:%d in %s mode', chalk.green('âœ“'), this.app.get('port'), this.app.get('env'));
      console.log('  Press CTRL-C to stop\n');
    });
  }
}

export default App;
